import React, { useMemo } from 'react';
import { cssDecorator } from '@front-toolkit/utils';
import { CarouselColorTheme } from '../types';

import styles from './Body.module.scss';

const cd = cssDecorator(styles);

interface BodyProps {
  /** Массив компонентов для размещения в слайдах */
  content: React.ReactNode[];

  /** Палитра цветов UI-элеметнов карусели */
  theme: CarouselColorTheme;

  /** Ширина слайда */
  slideWidth: number;

  /** Сдвиг ленты слайдов */
  offset: number;

  /** Растягивать содержимое по вертикали, так чтобы оно заходило под заголовок */
  fillHeader?: boolean;

  /** Флаг отображения уменьшенных (мобильных) слайдов */
  smallSlides?: boolean;
}

const Body: React.FC<BodyProps> = ({
  content,
  theme,
  slideWidth,
  fillHeader,
  smallSlides,
  offset = 0
}) => {
  const slideClassNames = useMemo(() => cd('body__slide', {
    fill_header: fillHeader,
    small_slides: smallSlides
  }), [fillHeader, smallSlides]);

  const bodyStyles = useMemo(
    () => ({
      '--ds-carousel-title-color': theme.color,
      transform: `translateX(${ offset * -1 }px)`
    } as React.CSSProperties),
    [theme.color, offset]
  );

  return (
    <div
      className={ styles.body }
      style={ bodyStyles }
    >
      { content.map((item, index) => (
        <div
          // eslint-disable-next-line react/no-array-index-key
          key={ `carousel-slide-${ index }` }
          className={ slideClassNames }
          style={ { width: slideWidth } }
        >
          { item }
        </div>
      )) }
    </div>
  );
};

export default Body;
